@import '../App.css';
.home .col {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.home a {
    text-decoration: none;
    color: black;
}
.col h1 {
    font-size: 3em;
    font-weight: 900;
}

.roles {
    background: inherit;
    height: 1rem;
    margin-bottom: 1.5rem;
}
.hero {
    border-radius: 250px;
    height: 100%;
}
.button {
    padding: 2.5%;
    color: white;
    font-weight: 700;
    border: none;
    font-size: 1.75em;
    cursor: pointer;
    transition: 0.2s all;
    background-color:rgb(232, 0, 0);
}

.home 
.button:hover {
    border-radius: 5px;
    border: none;
    -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);
    -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);
}
.home .right {
    gap: 1rem;
    flex-direction: row;
    align-items: center;
}
.contacts ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1rem;
}
.contact-icon {
    background-color: #ccc;
    padding: 1.5px;
    border-radius: 5px;
    transition: 0.2s all;
}
.contact-icon:hover {
    background-color: darkorange;
    transform: translateX(-2.5px);
}
.home {
    position: relative;
    /* background-image: url('../assets/skyline.jpeg'); */
    background-size: cover;
    background-repeat: no-repeat;
}
.home::before {
    position: absolute;
      top: 0px;
      right: 0px;
      bottom: 0px;
      left: 0px;
    opacity: 0.1;
}
@media all and (max-width: 755px) {
    .home
    .container {
        flex-direction: column-reverse;
        gap: 1.5rem;
    }
    .home
    .left,
    .home 
    .right {
        width: 100%;
        text-align: center;
    }
    .home
    .right {
        flex-direction: column;
    }
    .contacts ul {
        flex-direction: row;
    }
}
@media all and (max-width: 430px) {
    .home
    .right
    .hero {
        width: 100%;
    }
    
}